    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

ul,
li {
  list-style: none;
}

.Container_container__1QinQ {
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  padding: 20px 15px;
  height: 100vh;

  background-color: #e7ecf2;
}

/* ------ BORDER ------- */
.FeedbackCard_container__2gRHm {
  --border-width: 5px;

  position: relative;
  padding: 30px;
  /* width: 300px; */
  /* height: 200px; */
  font-family: Lato, sans-serif;
  /* font-size: 2.5rem; */
  /* text-transform: uppercase; */
  color: white;
  background: #222;
  border-radius: var(--border-width);
}

.FeedbackCard_container__2gRHm::after {
  position: absolute;
  content: '';
  top: calc(-1 * var(--border-width));
  left: calc(-1 * var(--border-width));
  z-index: -1;
  width: calc(100% + var(--border-width) * 2);
  height: calc(100% + var(--border-width) * 2);
  background: linear-gradient(
    60deg,
    hsl(224, 85%, 66%),
    hsl(269, 85%, 66%),
    hsl(314, 85%, 66%),
    hsl(359, 85%, 66%),
    hsl(44, 85%, 66%),
    hsl(89, 85%, 66%),
    hsl(134, 85%, 66%),
    hsl(179, 85%, 66%)
  );
  background-size: 300% 300%;
  background-position: 0 50%;
  border-radius: calc(2 * var(--border-width));
  animation: FeedbackCard_moveGradient__2deyn 4s alternate infinite;
}

@keyframes FeedbackCard_moveGradient__2deyn {
  50% {
    background-position: 100% 50%;
  }
}
/* ------ END BORDER ------- */

/* ------- BUTTON ------- */

.FeedbackOptions_button__1ySQR {
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.FeedbackOptions_button__1ySQR:before {
  content: '';
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: FeedbackOptions_glowing__EEIuL 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.FeedbackOptions_button__1ySQR:active {
  color: #000;
}

.FeedbackOptions_button__1ySQR:active:after {
  background: transparent;
}

.FeedbackOptions_button__1ySQR:hover:before {
  opacity: 1;
}

.FeedbackOptions_button__1ySQR:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes FeedbackOptions_glowing__EEIuL {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@media screen and (max-width: 799px) {
  .FeedbackOptions_button__1ySQR:not(:last-child) {
    margin-bottom: 25px;
  }
}

@media screen and (min-width: 554px) {
  .FeedbackOptions_button__1ySQR:not(:last-child) {
    margin-right: 25px;
  }
}

